@import url(https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800;900&display=swap);
.row{margin-left:20px;color:#fff}h2{padding-left:20px}.slider{position:relative}.slider:hover .slider__arrow-left{transition:400ms all ease-in-out;visibility:visible}.slider:hover .slider__arrow-right{transition:400ms all ease-in-out;visibility:visible}.slider__arrow-left{background-clip:content-box;padding:20px 0;box-sizing:border-box;transition:400ms all ease-in-out;cursor:pointer;width:80px;z-index:1000;position:absolute;left:0;top:0;height:100%;display:flex;align-items:center;justify-content:center;visibility:hidden}.slider__arrow-left:hover{background:rgba(20,20,20,.5);transition:400ms all ease-in-out}.slider__arrow-right{padding:20px 0;background-clip:content-box;box-sizing:border-box;transition:400ms all ease-in-out;cursor:pointer;width:80px;z-index:1000;position:absolute;right:0;top:0;height:100%;display:flex;align-items:center;justify-content:center;visibility:hidden}.slider__arrow-right:hover{background:rgba(20,20,20,.5);transition:400ms all ease-in-out}.arrow{transition:400ms all ease-in-out}.arrow:hover{transition:400ms all ease-in-out;transform:scale(1.5)}.row__posters{display:flex;overflow-y:hidden;overflow-x:scroll;padding:20px 0 20px 20px;scroll-behavior:smooth}.row__posters::-webkit-scrollbar{display:none}.row__poster{object-fit:contain;width:100%;max-height:144px;margin-right:10px;transition:transform 450ms;border-radius:4px}.row__poster:hover{transform:scale(1.08)}.row__posterLarge{max-height:320px}.row__posterLarge:hover{transform:scale(1.1);opacity:1}.row__arrow-left{position:absolute;top:0;left:20px;height:100%;width:32px;background:rgba(0,0,0,.2);display:flex;align-items:center}.row__arrow-right{position:absolute;top:0;right:0px;height:100%;width:32px;background:rgba(0,0,0,.2);display:flex;align-items:center}@media screen and (min-width: 1200px){.row__poster{max-height:160px}.row__posterLarge{max-height:360px}}@media screen and (max-width: 768px){.row__poster{max-height:100px}.row__posterLarge{max-height:280px}}
@keyframes fadeIn{from{opacity:0;transform:scale(0.5)}to{opacity:1;transform:scale(1)}}.modal{position:relative;width:auto;height:auto;max-width:800px;box-shadow:0px 3px 5px -1px rgba(0,0,0,.2),0px 5px 8px 0px rgba(0,0,0,.14),0px 1px 14px 0px rgba(0,0,0,.12);background:#111;overflow:hidden;border-radius:8px;transition:all 400ms ease-in-out 2s;animation:fadeIn 400ms;-ms-overflow-style:none;scrollbar-width:none}.modal::-webkit-scrollbar{display:none;visibility:hidden}.modal__poster-img{width:100%;height:auto}.modal__content{padding:40px}.modal__title{padding:0;font-size:40px;margin:16px 0}.modal__details{font-weight:600;font-size:18px}.modal__overview{font-size:20px;line-height:1.5}.modal__user-perc{color:#46d369}.wrapper-modal{z-index:-1;position:fixed;inset:0px;background-color:rgba(0,0,0,.2);-webkit-tap-highlight-color:transparent;display:flex;align-items:center;justify-content:center}.presentation{z-index:1200;position:absolute;top:0;left:0;background:rgba(0,0,0,.4)}.modal-close{position:absolute;right:20px;top:20px;cursor:pointer;z-index:1000}@media screen and (max-height: 768px){.wrapper-modal{align-items:unset;padding-top:2rem}.modal{overflow-y:scroll}}@media screen and (max-width: 768px){.modal__overview{font-size:16px}.modal__details{font-size:16px}.wrapper-modal{padding:0}.modal{overflow-y:scroll !important}}
.banner{color:#fff;object-fit:contain;height:448px}.banner__contents{margin-left:40px;padding-top:140px;height:190px}.banner__title{font-size:3rem;font-weight:800;padding-bottom:.5rem}.banner__description{width:45rem;line-height:1.3;padding-top:1rem;font-weight:500;font-size:1rem;max-width:400px;height:80px}.banner--fadeBottom{height:7.4rem;background-image:linear-gradient(180deg, transparent, rgba(37, 37, 37, 0.61), #111)}.banner__buttons{display:flex;flex-direction:row}.banner__button{display:flex;flex-direction:row;justify-content:start;align-items:center;cursor:pointer;outline:none;border:none;font-size:1rem;font-weight:700;border-radius:.2vw;padding:.4rem 1.8rem .4rem 1rem;margin-right:1rem}.banner__button:hover{color:#000;background-color:rgba(170,170,170,.9);transition:all .2s}.play{background-color:#fff;color:#000}.info{background-color:rgba(109,109,110,.7);color:#fff}.info:hover{background-color:#4a4a4a;color:#fff}.space{margin-left:4px}@media(min-width: 1500px){.banner{position:relative;height:600px}.banner--fadeBottom{position:absolute;bottom:0;width:100%;height:40rem}}@media(max-width: 768px){.banner__contents{width:-webkit-min-content !important;width:-moz-min-content !important;width:min-content !important;padding-left:2.3rem;margin-left:0px !important}.banner__description{font-size:.8rem !important;width:auto !important}.info{text-align:start;padding-right:1.2rem}.space{margin-left:6px}.banner__button{font-size:.8rem !important;border-radius:4px !important}}
.nav{position:fixed;top:0;width:100%;height:30px;z-index:1;padding:20px;display:flex;justify-content:space-between;align-items:center;transition-timing-function:ease-in;transition:all .5s}.nav__black{background-color:#111}.nav__logo{position:fixed;left:40px;width:80px;object-fit:contain}.nav__avatar{position:fixed;right:40px;width:30px;object-fit:contain}
*{margin:0}.app{background:#111;font-family:"Inter",sans-serif}
